@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__BdaUe {
  border-radius: 0.5rem;
  box-shadow: 0px 5px 20px rgba(25, 25, 25, 0.1);
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-width: 30rem;
  max-width: 30rem;
}

.recipe_recipe__BdaUe h1 {
  max-width: 80%;
  padding: 2rem 0rem;
}

.recipe_recipe__BdaUe p {
  font-weight: 1000;
  margin: 0;
}

.recipe_recipe__BdaUe ul {
  padding: 2rem 0rem;
  max-width: 80%;
}

img {
  border-radius: 50%;
  width: 15rem;
  height: 15rem;
  object-fit: cover;
  margin: 2rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  background-image: linear-gradient(to top, #ebbba7 0%, #cfc7f8 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 0.5rem;
  outline: none;
  font-size: 1.5rem;
}

.search-button {
  background: #ebbba7;
  border: none;
  padding: 1rem;
  font-size: 1rem;
  border-radius: 10%;
  outline: none;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

.search-button:hover {
  background: #cfc7f8;
}

.recipes {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
body {
  font-family: "Source Sans Pro", sans-serif;
}

